@import url(https://fonts.googleapis.com/css?family=Quicksand);

html.wf-materialicons-n4-active wf-active {
    width: 100%;
    height: 100%;
    margin: none;
}



#loginBackground {
    background-image: linear-gradient(#BDFFF3, #4AC29A);
    max-width: 100% !important;
    height: 100vh;
}

#formContainer {
    background-color: rgb(255, 255, 255, .6);
    padding: 5%;
    border-radius: 10px;
    margin: auto;
    margin-top: 10%;
}

#usernameLogin, #passwordLogin {
    background-color: #4AC29A;
    opacity: .75;
    border: none;
    color: white;
}

#formButton {
    background-color: #4AC29A;
    border: none; 
}

@media screen and (max-width: 947px) {
    #formContainer {
        margin-top: 15%;
    }
}

@media screen and (max-width: 767px) {
    #formContainer {
        margin-top: 20%;
    }
}

@media screen and (max-width: 530px) {
    #formContainer {
        margin-top: 30%;
    }
}
#registerBackground {
    background-image: linear-gradient(#BDFFF3, #4AC29A);
    max-width: 100% !important;
    height: 100%;
}

#formContainer {
    background-color: rgb(255, 255, 255, .6);
    padding: 5%;
    border-radius: 10px;
    margin: 5% auto; 
    margin-bottom: 50%;
}

#nameReg, #usernameReg, #emailReg, #dobReg, #passwordReg, #confirmPasswordReg {
    background-color: #4AC29A;
    opacity: .75;
    border: none;
    color: white;
}

#formButton {
    background-color: #4AC29A;
    border: none; 
}
